        </li>
        <li draggable="true" @dragstart="dragStart" data-name="Br">
          <mu-icon value="keyboard_return"/>
          换行
        </li>
        <li draggable="true" @dragstart="dragStart" data-name="Div">
          <mu-icon value="check_box_outline_blank"/>
          div
        </li>
        <li draggable="true" @dragstart="dragStart" data-name="button">
          <mu-icon value="panorama_fish_eye"/>
          按钮
        </li>
        <li draggable="true" @dragstart="dragStart" data-name="list">
          <mu-icon value="dehaze"/>
          列表
        </li>
        <li draggable="true" @dragstart="dragStart" data-name="SearchBar">
          <mu-icon value="search"/>
          搜索
        </li>
      </ul>
    </div>
    <div v-if="activeUI === 'page'">
      <div @click="selectPage(key)" class="pageItem" v-for="(page, key) in pages" :key="key">
        <div style="margin-left: 20px">{{page['name']}}</div>
        <el-divider class="componentDivider"></el-divider>
      </div>
    </div>
  </section>
</template>
<script>
import museUiList from './list/muse-ui'
import mintUiList from './list/mint-ui'
import iViewUiList from './list/iview-ui'

export default {
  name: 'components',
  data() {
    return {}
  },
  mounted() {

  },
  methods: {
    selectPage(key) {
      // console.log(this.pages[key])
      this.currentPage = this.pages[key]
    },
    handleTabChange(val) {
      this.activeUI = val
    },
    getComponent(e) {
      if (!e)
        return
      if (e.localName !== 'li')
        return this.getComponent(e.parentElement)
      else
        return e
    },
    removeDom(e) {
      if (e && e.parentElement)
        e.parentElement.removeChild(e)
    },
    dragStart(e) {
      let componentName = e.target.getAttribute('data-name')
      let info = {
        name: componentName,
        ui: this.activeUI
      }
      e.dataTransfer.setData('info', JSON.stringify(info))
    }
  },
  computed: {
    currentPage:{
      get() {
        return this.$store.state.currentPage
      },
      set(currentPage) {
        this.$store.commit('setState', {
          currentPage: currentPage
        })
      }
    },
    pages: {
      get() {
        return this.$store.state.pages
      }
    },
    activeUI: {
      get() {
        return this.$store.state.activeUI
      },
      set(val) {
        this.$store.commit('setState', {
          activeUI: val
        })
      }
    }
  },
  components: {
    ...museUiList,
    ...mintUiList,
    ...iViewUiList
  }
}
</script>
<style lang="less" scoped>
.components-list {
  list-style: none;
  padding: 0;
  color: #2196f3;
  margin: 0 0 0 15px;
}

.components-list * {
  cursor: move !important;
}

.components-list > li {
  min-height: 36px;
  font-size: 18px;
  -webkit-user-select: none;
  transform: scale(0.7) translateX(-15%);
  padding-bottom: 10px;
  transition: transform .2s;

  &:hover {
    transform: scale(1) translateX(5%);
  }

  i {
    vertical-align: middle;
  }
}

.components-list.iview-ui > li {
  transform: scale(0.8) translateX(-5%);

  &:hover {
    transform: scale(1.1) translateX(10%);
  }
}

/deep/ .mu-select-field {
  .mu-dropDown-menu {
    display: block;
    width: 100%;
    height: 32px;
    color: #3c8cfd;
  }

  .mu-dropDown-menu-text {
    line-height: 32px;
    height: 32px;
    padding-left: 0px;
    padding-right: 24px;
    word-wrap: break-word;
    overflow: hidden;
    color: #3c8cfd;
  }

  .mu-dropDown-menu-line {
    color: #3c8cfd;
  }

  .mu-dropDown-menu-icon {
    right: 0;
    top: 6px;
    color: #3c8cfd;
  }
}
/deep/ .componentDivider{
  background-color: #616161;
  width: 150px;
  margin-left: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.pageItem{
  height: 30px;
  cursor: pointer;
}
</style>



